"use client";

import React, { useState, useEffect } from 'react';
import { StoreBookEntry, StockBalance } from '@/lib/models/storeBook';
import StoreBookTable from '@/components/store-book/StoreBookTable';
import StoreBookForm from '@/components/store-book/StoreBookForm';
import StoreBookFilters from '@/components/store-book/StoreBookFilters';
import StockBalanceTable from '@/components/store-book/StockBalanceTable';
import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import Image from 'next/image';

export default function StoreBookPage() {
  const [entries, setEntries] = useState<StoreBookEntry[]>([]);
  const [stockBalances, setStockBalances] = useState<StockBalance[]>([]);
  const [filteredEntries, setFilteredEntries] = useState<StoreBookEntry[]>([]);
  const [selectedEntry, setSelectedEntry] = useState<StoreBookEntry | undefined>();
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Fetch entries and stock balances
  const fetchData = async () => {
    try {
      setLoading(true);
      const response = await fetch('/api/store-book');
      const data = await response.json();
      
      if (!response.ok) {
        throw new Error(data.error || 'Failed to fetch data');
      }
      
      setEntries(data.entries);
      setStockBalances(data.stockBalances || []);
      setFilteredEntries(data.entries);
      setError(null);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  // Handle create/update entry
  const handleSave = async (entry: StoreBookEntry) => {
    try {
      const method = entry.id && entry.id !== '' ? 'PUT' : 'POST';
      const response = await fetch('/api/store-book', {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(entry),
      });

      const data = await response.json();
      
      if (!response.ok) {
        throw new Error(data.error || 'Failed to save entry');
      }

      await fetchData(); // Refresh both entries and stock balances
      setIsFormOpen(false);
      setSelectedEntry(undefined);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to save entry');
    }
  };

  // Handle delete entry
  const handleDelete = async (id: string) => {
    try {
      const response = await fetch('/api/store-book', {
        method: 'DELETE',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id }),
      });

      const data = await response.json();
      
      if (!response.ok) {
        throw new Error(data.error || 'Failed to delete entry');
      }

      await fetchData(); // Refresh both entries and stock balances
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to delete entry');
    }
  };

  // Handle edit entry
  const handleEdit = (entry: StoreBookEntry) => {
    setSelectedEntry(entry);
    setIsFormOpen(true);
  };

  // Handle filters
  const handleFilterChange = (filters: {
    query: string;
    transactionType: string;
    dateRange: { start: string; end: string };
  }) => {
    let filtered = [...entries];

    // Filter by search query
    if (filters.query) {
      const query = filters.query.toLowerCase();
      filtered = filtered.filter(
        entry => entry.itemName.toLowerCase().includes(query) ||
                entry.vendor?.toLowerCase().includes(query) ||
                entry.remarks?.toLowerCase().includes(query)
      );
    }

    // Filter by transaction type
    if (filters.transactionType && filters.transactionType !== 'all') {
      filtered = filtered.filter(
        entry => entry.transactionType === filters.transactionType
      );
    }

    // Filter by date range
    if (filters.dateRange.start || filters.dateRange.end) {
      filtered = filtered.filter(entry => {
        const entryDate = new Date(entry.date);
        const start = filters.dateRange.start ? new Date(filters.dateRange.start) : null;
        const end = filters.dateRange.end ? new Date(filters.dateRange.end) : null;

        if (start && end) {
          return entryDate >= start && entryDate <= end;
        } else if (start) {
          return entryDate >= start;
        } else if (end) {
          return entryDate <= end;
        }
        return true;
      });
    }

    setFilteredEntries(filtered);
  };

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Hero Section with Logo */}
      <Card className="mb-8 overflow-hidden">
        <div className="relative h-64 bg-gradient-to-r from-gray-900 to-gray-800">
          <div className="absolute inset-0 bg-black/50" />
          <div className="relative z-10 h-full flex flex-col justify-center items-center px-6">
            {/* Logo */}
            <div className="mb-4">
              <Image
                src="/rajbari-logo.svg"
                alt="The Rajbari Logo"
                width={120}
                height={120}
                className="filter brightness-0 invert"
              />
            </div>
            
            <h1 className="text-3xl md:text-4xl font-bold text-white mb-2 text-center">
              The Rajbari - Restaurant Godown Store Book
            </h1>
            <p className="text-gray-200 max-w-2xl text-center">
              Efficiently manage your restaurant's inventory with our digital store book system. 
              Track inward and outward movements, maintain stock levels, and generate insights.
            </p>
          </div>
        </div>
      </Card>

      {/* Error Message */}
      {error && (
        <div className="bg-red-50 border border-red-200 text-red-800 rounded-lg p-4 mb-6">
          {error}
        </div>
      )}

      {/* Main Content Tabs */}
      <Tabs defaultValue="transactions" className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="transactions">Store Transactions</TabsTrigger>
          <TabsTrigger value="stock">Stock Balance</TabsTrigger>
        </TabsList>

        <TabsContent value="transactions" className="space-y-6">
          {/* Filters */}
          <StoreBookFilters onFilterChange={handleFilterChange} />

          {/* Add New Button */}
          <div className="mb-6">
            <Button
              onClick={() => {
                setSelectedEntry(undefined);
                setIsFormOpen(true);
              }}
              className="bg-black hover:bg-gray-800 text-white"
            >
              Add New Entry
            </Button>
          </div>

          {/* Transactions Table */}
          {loading ? (
            <Card className="p-8">
              <div className="flex justify-center items-center">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900"></div>
              </div>
            </Card>
          ) : (
            <StoreBookTable
              entries={filteredEntries}
              onEdit={handleEdit}
              onDelete={handleDelete}
            />
          )}
        </TabsContent>

        <TabsContent value="stock" className="space-y-6">
          {/* Stock Balance Table */}
          {loading ? (
            <Card className="p-8">
              <div className="flex justify-center items-center">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900"></div>
              </div>
            </Card>
          ) : (
            <StockBalanceTable stockBalances={stockBalances} />
          )}
        </TabsContent>
      </Tabs>

      {/* Form Dialog */}
      <StoreBookForm
        isOpen={isFormOpen}
        onClose={() => {
          setIsFormOpen(false);
          setSelectedEntry(undefined);
        }}
        onSave={handleSave}
        entry={selectedEntry}
      />
    </div>
  );
}
