"use client";

import React from 'react';
import { StockBalance } from '@/lib/models/storeBook';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';

interface StockBalanceTableProps {
  stockBalances: StockBalance[];
}

export const StockBalanceTable: React.FC<StockBalanceTableProps> = ({ stockBalances }) => {
  const getStockStatus = (stock: number) => {
    if (stock === 0) return { label: 'Out of Stock', variant: 'destructive' as const };
    if (stock < 10) return { label: 'Low Stock', variant: 'secondary' as const };
    return { label: 'In Stock', variant: 'default' as const };
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <span>📦</span>
          Current Stock Balance
        </CardTitle>
      </CardHeader>
      <CardContent>
        {stockBalances.length === 0 ? (
          <div className="text-center py-8 text-gray-500">
            No stock data available. Add some entries to see stock balances.
          </div>
        ) : (
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Item Name</TableHead>
                  <TableHead>Current Stock</TableHead>
                  <TableHead>Unit</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Last Updated</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {stockBalances.map((stock, index) => {
                  const status = getStockStatus(stock.currentStock);
                  return (
                    <TableRow key={index}>
                      <TableCell className="font-medium">
                        {stock.itemName}
                      </TableCell>
                      <TableCell className="text-right font-mono">
                        {stock.currentStock}
                      </TableCell>
                      <TableCell>
                        {stock.unit}
                      </TableCell>
                      <TableCell>
                        <Badge variant={status.variant}>
                          {status.label}
                        </Badge>
                      </TableCell>
                      <TableCell className="text-sm text-gray-500">
                        {new Date(stock.lastUpdated).toLocaleDateString()}
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </div>
        )}
      </CardContent>
    </Card>
  );
};

export default StockBalanceTable;
