"use client";

import React, { useState, useEffect } from 'react';
import { StoreBookEntry } from '@/lib/models/storeBook';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
  DialogDescription,
} from '@/components/ui/dialog';

interface StoreBookFormProps {
  isOpen: boolean;
  onClose: () => void;
  onSave: (entry: StoreBookEntry) => void;
  entry?: StoreBookEntry;
}

export const StoreBookForm: React.FC<StoreBookFormProps> = ({
  isOpen,
  onClose,
  onSave,
  entry
}) => {
  const [formData, setFormData] = useState<Partial<StoreBookEntry>>({
    date: new Date().toISOString().split('T')[0],
    transactionType: 'inward',
    quantity: 0,
    unit: 'kg'
  });
  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    if (entry) {
      setFormData({
        ...entry,
        date: new Date(entry.date).toISOString().split('T')[0]
      });
    } else {
      setFormData({
        date: new Date().toISOString().split('T')[0],
        transactionType: 'inward',
        quantity: 0,
        unit: 'kg'
      });
    }
  }, [entry, isOpen]);

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.itemName?.trim()) {
      newErrors.itemName = 'Item name is required';
    }
    if (!formData.date) {
      newErrors.date = 'Date is required';
    }
    if (!formData.transactionType) {
      newErrors.transactionType = 'Transaction type is required';
    }
    if (!formData.quantity || formData.quantity <= 0) {
      newErrors.quantity = 'Quantity must be greater than 0';
    }
    if (!formData.unit) {
      newErrors.unit = 'Unit is required';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (validateForm()) {
      const finalData: StoreBookEntry = {
        id: entry?.id || '',
        date: formData.date!,
        itemName: formData.itemName!,
        transactionType: formData.transactionType as 'inward' | 'outward' | 'adjustment',
        quantity: Number(formData.quantity),
        unit: formData.unit!,
        vendor: formData.vendor,
        remarks: formData.remarks
      };
      onSave(finalData);
    }
  };

  const handleChange = (
    field: keyof StoreBookEntry,
    value: string | number
  ) => {
    setFormData(prev => ({
      ...prev,
      [field]: field === 'quantity' ? Number(value) : value
    }));
    
    // Clear error when field is updated
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>
            {entry ? 'Edit Store Entry' : 'Add New Store Entry'}
          </DialogTitle>
          <DialogDescription>
            Fill in the details for the store book entry.
          </DialogDescription>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="date">Date</Label>
            <Input
              id="date"
              type="date"
              value={formData.date || ''}
              onChange={(e) => handleChange('date', e.target.value)}
              className={errors.date ? 'border-red-500' : ''}
            />
            {errors.date && (
              <p className="text-red-500 text-sm">{errors.date}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="itemName">Item Name</Label>
            <Input
              id="itemName"
              type="text"
              value={formData.itemName || ''}
              onChange={(e) => handleChange('itemName', e.target.value)}
              className={errors.itemName ? 'border-red-500' : ''}
              placeholder="Enter item name"
            />
            {errors.itemName && (
              <p className="text-red-500 text-sm">{errors.itemName}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="transactionType">Transaction Type</Label>
            <Select
              value={formData.transactionType}
              onValueChange={(value) => handleChange('transactionType', value)}
            >
              <SelectTrigger className={errors.transactionType ? 'border-red-500' : ''}>
                <SelectValue placeholder="Select transaction type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="inward">Inward</SelectItem>
                <SelectItem value="outward">Outward</SelectItem>
                <SelectItem value="adjustment">Adjustment</SelectItem>
              </SelectContent>
            </Select>
            {errors.transactionType && (
              <p className="text-red-500 text-sm">{errors.transactionType}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="quantity">Quantity</Label>
            <Input
              id="quantity"
              type="number"
              min="1"
              step="1"
              value={formData.quantity || ''}
              onChange={(e) => handleChange('quantity', e.target.value)}
              className={errors.quantity ? 'border-red-500' : ''}
            />
            {errors.quantity && (
              <p className="text-red-500 text-sm">{errors.quantity}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="unit">Unit</Label>
            <Select
              value={formData.unit}
              onValueChange={(value) => handleChange('unit', value)}
            >
              <SelectTrigger className={errors.unit ? 'border-red-500' : ''}>
                <SelectValue placeholder="Select unit" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="kg">Kilograms</SelectItem>
                <SelectItem value="l">Liters</SelectItem>
                <SelectItem value="pcs">Pieces</SelectItem>
                <SelectItem value="boxes">Boxes</SelectItem>
              </SelectContent>
            </Select>
            {errors.unit && (
              <p className="text-red-500 text-sm">{errors.unit}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="vendor">Vendor (Optional)</Label>
            <Input
              id="vendor"
              type="text"
              value={formData.vendor || ''}
              onChange={(e) => handleChange('vendor', e.target.value)}
              placeholder="Enter vendor name"
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="remarks">Remarks (Optional)</Label>
            <Input
              id="remarks"
              type="text"
              value={formData.remarks || ''}
              onChange={(e) => handleChange('remarks', e.target.value)}
              placeholder="Add remarks"
            />
          </div>

          <DialogFooter>
            <Button type="button" variant="outline" onClick={onClose}>
              Cancel
            </Button>
            <Button type="submit">
              {entry ? 'Update' : 'Save'}
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
};

export default StoreBookForm;
